<template><section><h1>popover</h1>
<template>
    <coding :code="code" title="popover" content="简单的popover，可以内嵌html。">
        <y-popover title="Title">
            <y-button slot="html">123</y-button>
            <template slot="content">
                <p>asdfasdf</p>
                <p>asdfasdf</p>
            </template>
        </y-popover>
    </coding>
</template>

<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>slot::html</td>
<td>触发目标（必须）</td>
<td><code>slot</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>slot:content</td>
<td>气泡内容（不包括气泡标题）</td>
<td><code>slot</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>title</td>
<td>气泡标题（必须）</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>trigger</td>
<td>触发气泡事件名称</td>
<td><code>String</code></td>
<td><code>click</code></td>
<td><code>click</code>,<code>hover</code>,<code>focus</code></td>
</tr>
<tr>
<td>placement</td>
<td>气泡位置</td>
<td><code>String</code></td>
<td><code>top</code></td>
<td><code>top</code>,<code>top-left</code>,<code>top-right</code>,<code>bottom-left</code>,<code>bottom-right</code>,<code>left-top</code>,<code>left-bottom</code>,<code>right-top</code>,<code>right-bottom</code></td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            code:
`<y-popover title="Title">
    <span slot="html">123</span>
    <template slot="content">
        <p>asdfasdf</p>
        <p>asdfasdf</p>
    </template>
</y-popover>`
        }
    }
}
</script>


